
import { cookies } from 'next/headers';
import axios from 'axios';
import { redirect } from 'next/';

export default async function AdminPanel() {
  const token = (await cookies()).get('token')?.value;

  if (!token) {
    return (
      <div>
        <h1>Доступ заборонено</h1>
        <p>Будь ласка, увійдіть, щоб отримати доступ до адмін панелі.</p>
      </div>
    );
  }

  try {
    const res = await axios.get('http://localhost:5200/api/auth/check-admin', {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    if (res.data.role !== 'admin') {
      return (
        <div>
          <h1>Доступ заборонено</h1>
          <p>Ви не маєте прав для доступу до цієї сторінки.</p>
        </div>
      );
    }

    return (
      <div>
        <h1>Адмін Панель</h1>
        <p>Тільки для адміністраторів.</p>
      </div>
    );
  } catch (err) {
    redirect("/")
  }
}
